import React, { useState } from "react";
import { BrowserRouter, Route } from "react-router-dom";
import NavBar from "./components/navBar/navBar";
import Shop from "./components/shop";
import Orders from "./components/orders";
import SignIn from "./components/signin";
import Dashboard from "./components/dashboard";

const App = () => {
  const [loginState, updateLoginState] = useState(false);
  const updatedLoginState = (updatedState) => {
    updateLoginState(updatedState);
  };

  const Logout = () => {
    updateLoginState(false);
  };

  return (
    <div>
      <BrowserRouter>
        <NavBar loginState={loginState} />
        <div id="content">
          <Route path="/" exact>
            <Shop state={loginState} />
          </Route>
          <Route path="/orders">
            <Orders loginState={loginState} />
          </Route>
          <Route path="/signin">
            <SignIn loginState={loginState} onSignIn={updatedLoginState} />
          </Route>
          <Route path="/dashboard">
            <Dashboard loginState={loginState} onLogout={Logout} />
          </Route>
        </div>
      </BrowserRouter>
    </div>
  );
};
export default App;
