import React, { useState } from "react";
import { Link } from "react-router-dom";
// import "../user.json";
// import userData from "./jsonData";
import "./navBar/navbar.css";

const SignIn = (props) => {
  let loginState = false;
  const [name, updateName] = useState("");
  const [password, updatePassword] = useState("");
  const [nextPagePath, updateNextPagePath] = useState("/signin");

  const user = {
    id: "abc",
    password: "abc"
  };

  const signIn = () => {
    name === "" || password === ""
      ? alert("Please Fill Fields")
      : name === user.id && password === user.password
      ? (loginState = true)
      : alert("incorrect credentials");
    loginState ? updateNextPagePath("/") : updateNextPagePath("/signin");
    props.onSignIn(loginState);
  };

  return (
    <>
      <h1>SignIn</h1>
      <form id="signin-form" onSubmit={(e) => e.preventDefault()}>
        <input
          placeholder="Name"
          value={name}
          onChange={(e) => {
            updateName(e.target.value);
          }}
        />
        <input
          type="password"
          placeholder="password"
          value={password}
          onChange={(e) => {
            updatePassword(e.target.value);
          }}
        />
        <Link to={nextPagePath}>
          <input
            id="login-button"
            type="button"
            onClick={signIn}
            value="login"
          />
        </Link>
      </form>
    </>
  );
};
export default SignIn;
