import React from "react";

const Orders = (props) => {
  const loginState = props.loginState;
  let message = " ";
  loginState
    ? (message = "View Orders")
    : (message = "Please Login To view Orders");
  return (
    <>
      <h1 id="order-title">Orders</h1>

      {loginState ? (
        <table className="table heading-background-color">
          <thead className="thead-dark ">
            <tr>
              <th scope="col">#</th>
              <th scope="col">Customer</th>
              <th scope="col">Order</th>
              <th scope="col">Qty</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th scope="row">1</th>
              <td>Mark</td>
              <td>Nivea Cream</td>
              <td>2</td>
            </tr>
            <tr>
              <th scope="row">2</th>
              <td>Jacob</td>
              <td>Hand Moisturiser</td>
              <td>1</td>
            </tr>
            <tr>
              <th scope="row">3</th>
              <td>Larry</td>
              <td>Face wash</td>
              <td>5</td>
            </tr>
          </tbody>
        </table>
      ) : (
        <p id="message">{message}</p>
      )}
    </>
  );
};
export default Orders;
