import React from "react";
import { Link } from "react-router-dom";
import "./navbar.css";

const NavBar = (props) => {
  let navAccountLiName = "Sign In";
  let accountPageLink = "/signin";
  // props.loginState ? (
  //     navAccountLiName = "Account",
  //     accountPageLink = "/account"
  //  ) : (
  //      navAccountLiName = "SignIn",
  //      accountPageLink = "/signin"
  //      );
  props.loginState
    ? (navAccountLiName = "Account")
    : (navAccountLiName = "Sign In");

  props.loginState
    ? (accountPageLink = "/dashboard")
    : (accountPageLink = "/signin");

  return (
    <ul id="navbar">
      <li>
        <Link className="link" to="/">
          Shop
        </Link>
      </li>
      <li>
        <Link className="link" to="/orders">
          Orders
        </Link>
      </li>
      <li>
        <Link className="link" to={accountPageLink}>
          {navAccountLiName}
        </Link>
      </li>
    </ul>
  );
};
export default NavBar;
